import { Component, OnInit, AfterViewInit, NgModule } from '@angular/core';
import { ScriptLoaderService } from '../../_services/script-loader.service';
import { CommonModule } from "@angular/common";
import {CommonProvider} from "../../layouts/common";

@NgModule({
  imports: [
    CommonModule
  ]
})

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
})
export class ProfileComponent implements OnInit, AfterViewInit {
	
	casas = [];

  constructor(private _script: ScriptLoaderService,private common : CommonProvider) { 
  		this.common.casasEvet.subscribe(data => {
  				console.log(data);
  				if (data.casas != undefined) {
  					this.casas = data.casas;

  				}else{
  					if(data.casasFiltro != undefined){
  						this.casas = data.casasFiltro;
  					}
  				}
  		});

  		
  }
  

  ngOnInit() {
  	// this.casas = JSON.parse(localStorage.getItem("casas"));
  	console.log(this.common.casas);
  	this.casas = this.common.casas
  }

  ngAfterViewInit() {
    this._script.load('./assets/js/scripts/profile-demo.js');
  }

}



