import { Component, OnInit } from '@angular/core';
import {CommonProvider} from "../../layouts/common";

import * as $ from "jquery";

declare var jQuery:any;
declare var $:any;

@Component({
  selector: '[app-invoice]',
  templateUrl: './invoice.component.html',
})
export class InvoiceComponent implements OnInit {
mensajeMen = "";
ciudades = [
"Madrid",
"Barcelona",
"Valencia", 
"Sevilla" ,
"Zaragoza" ,
"Málaga" ,
"Murcia" ,
"Palma de mallorca",
"Las palmas de gran canaria", 
"Bilbao" ,
"Teruel",
"Burgos",
"Segovia",
"Toledo",
"Santander",
"Salamanca",
"Santiago de compostela", 
"Cáceres",
"San Sebastián",
 "Santa Cruz de Tenerife", 
 "Tenerife", 
 "Canarias"
  ];

  casas = [];
  ciudad_buscar = "";

  constructor(private common : CommonProvider) { }

  ngOnInit() {

    $("#tags").autocomplete({
      source: this.ciudades
    });

    
    $("#tags").click(function(){$("#tags").attr("placeholder","")});
    $("#tags").focusout(function(){$("#tags").attr("placeholder","¿Donde buscas tu proxima inversion?")});
    let a = localStorage.ciudad;
    if (a != undefined) {
      localStorage.removeItem("ciudad");
    }

    }


  buscar(){
  	// this.helper.toggle(2);
  	
    let au = $("#tags").val();
    console.log(au);
    let ai = this.buscar_elemento(au)
    console.log(ai);
    if (ai != undefined) {
      console.log("buscar");
      this.common.cambiarCasas({ciudad: au});
      this.common.toggle({pagina:2,ciudad:au});
      
    }else{
      console.log("no sse encuentra");
      if (au == "" || au == " ") {
        this.mensajeMen = "Debe Ingresar Una Ciudad";
      }else{
        this.mensajeMen = "Verifique el nombre de la ciudad y vuelva a intentar";
      }
      $('#mesajeM').modal('toggle'); 
    }
  	// Helpers.toggle(2)
  }

  filterItems(query) {
  return this.ciudades.filter(function(el) {
      return el.toLowerCase().indexOf(query.toLowerCase()) > -1;
  })
  }

  buscar_elemento(ciudad: String){
    return this.ciudades.find(
      function(element) {return element.toLowerCase() == ciudad.toLowerCase();}) 
  }


 onKeydown() {
  
  console.log(this.ciudad_buscar);
}

mapas(){
  this.common.toggle({pagina:3});
}

// SNACK

snack(){
  this.common.toggle({pagina:4});
}




}



