import { Component, AfterViewInit } from '@angular/core';
import {CommonProvider} from "../common";
import * as $ from "jquery";

declare var jQuery:any;
declare var $:any;


@Component({
  selector: '[app-header]',
  templateUrl: './app-header.component.html',
})
export class AppHeader implements AfterViewInit {
  ciudad = null;

  


  constructor(
  	private common : CommonProvider
  ) {

    

   }

  ngAfterViewInit() {

    // initialize layout: handlers, menu ...
    console.log("ciudadss");
    setTimeout(this.buscar,1000);

    this.ciudad = localStorage.getItem("ciudad");



    

  }

  buscar(){
    let ciudades = ["Madrid", "Barcelona", "Valencia", "Sevilla" , "Zaragoza" ,
     "Málaga" , "Murcia" , "Palma de mallorca", "Las palmas de gran canaria",
      "Bilbao" , "Teruel", "Burgos", "Segovia", "Toledo", "Santander", "Salamanca",
       "Santiago de compostela", "Cáceres", "San Sebastián", "Santa Cruz de Tenerife",
        "Tenerife", "Canarias"]; 
    let a = $("#tags2");
    console.log(a);
    if (a.length != 0) {
      $("#tags2").autocomplete({
        source: ciudades
      });
      console.log("entro en buscar");
      console.log(ciudades);
    }else{
      console.log("buscar 1");
      setTimeout(this.buscar,1000);
    }
    
  }

	inicio(){
		this.common.toggle({pagina:1});
	}

  find(){
    let au = $("#tags2").val();
    console.log(au);
    let ai = this.buscar_elemento(au)
    console.log(ai);
    if (ai != undefined) {
      console.log("buscar");
      this.ciudad = ai;
      this.common.cambiarCasas({ciudad: au});
      localStorage.setItem("ciudad",ai);
      
    }else{
      console.log("no sse encuentra")
    }
  }

  buscar_elemento(ciudad: String){
    let ciudades = ["Madrid", "Barcelona", "Valencia", "Sevilla" , "Zaragoza" ,
     "Málaga" , "Murcia" , "Palma de mallorca", "Las palmas de gran canaria",
      "Bilbao" , "Teruel", "Burgos", "Segovia", "Toledo", "Santander", "Salamanca",
       "Santiago de compostela", "Cáceres", "San Sebastián", "Santa Cruz de Tenerife",
        "Tenerife", "Canarias"]; 
    return ciudades.find(
      function(element) {return element.toLowerCase() == ciudad.toLowerCase();}) 
  }

}



